input {
  width: 370px;
  height: 60px;
  background: #FFFFFF;
  border: 1px solid #E0E0E5;
  box-sizing: border-box;
  border-radius: 4px;
  outline: none;
  padding: 5px 15px 5px 75px;
  transition: all .5s;

  &::placeholder {
    font-size: 16px;
    line-height: 32px;
    color: #424753;
    mix-blend-mode: normal;
    opacity: 0.5;
  }

  &:focus, &:active {
    border: 1px solid #13AA57;
    box-shadow: 0 0 4px rgba(19, 170, 87, 0.53);
  }

  &.error {
    border: 1px solid #EB5757;
    box-shadow: 0 0 4px rgba(235, 87, 87, 0.53);
  }
}