.about {
  background: $light;
  padding-top: 60px;
  padding-bottom: 80px;

  @media screen and (max-width: 1199px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .flex__wrap {

    justify-content: space-between;

    @media screen and (max-width: 1199px) {
      flex-direction: column;
      align-content: center;
    }

    .left {
      flex-basis: 60%;

      @media screen and (max-width: 1199px) {
        margin-bottom: 30px;
      }

      h2 {
        font-weight: bold;
        font-size: 28px;
        line-height: 33px;
        color: #19AD56;
        margin-top: 15px;
        margin-bottom: 30px;
      }

      .description {
        font-weight: normal;
        font-size: 16px;
        line-height: 32px;
        color: $mainText;
      }
    }

    .right {
      flex-basis: 35%;
      text-align: right;
      position: relative;

      @media screen and (max-width: 1199px) {
        text-align: center;
      }

      @media screen and (max-width: 767px) {
        width: 100%;
      }

      &:before {
        position: absolute;
        content: "";
        right: 0;
        top: 0;
        width: 350px;
        height: 320px;
        background: #13AA57;
        border-radius: 30px;
        transform: rotate(-6.99deg);
        z-index: 1;
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.4);

        @media screen and (max-width: 1199px) {
          right: calc(50% - 175px);
          top: calc(50% - 160px);
        }

        @media screen and (max-width: 767px) {
          width: 280px;
          height: 250px;
          right: calc(50% - 140px);
          top: calc(50% - 125px);
        }
      }

      img {
        width: 350px;
        height: 320px;
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.4);
        border-radius: 30px;
        position: relative;
        z-index: 2;

        @media screen and (max-width: 767px) {
          width: 280px;
          height: 250px;
        }
      }
    }
  }
}