button {
  width: 769px;
  height: 60px;
  background: linear-gradient(264.98deg, #55BD46 -0.09%, #13AA57 100%);
  border-radius: 40px;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  margin-top: 10px;
  cursor: pointer;
  border: none;
  transition: all .5s;
  transition-duration: .5s;

  &:hover {
    background: linear-gradient( #1279ED, #1279ED );
  }
}