.form {
  background: url(../img/bg2.png) no-repeat;
  background-position-x: left;
  padding-top: 80px;
  padding-bottom: 40px;
  box-sizing: border-box;
  text-align: center;
  background-size: contain;

  @media screen and (max-width: 767px) {
    background-position-y: bottom;
  }

  h2 {
    font-weight: bold;
    font-size: 28px;
    line-height: 33px;
    color: #19AD56;
    margin-top: 0;
    margin-bottom: 25px;
  }

  form {
    width: 850px;
    height: 396px;
    padding: 0 45px 25px;
    display: block;
    margin: 0 auto;

    @media screen and (max-width: 1023px) {
      width: 90%;
      height: auto;

      input, button {
        width: 100%;
      }
    }

    @media screen and (max-width: 767px) {
      padding: 0;
    }

    h4 {
      font-weight: normal;
      font-size: 22px;
      line-height: 34px;
      text-align: center;
      color: #607D8B;
      margin: 0 0 25px 0;

      @media screen and (max-width: 767px) {
        font-size: 20px;
      }
    }

    h5 {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      text-transform: uppercase;
      color: #13AA57;
      position: relative;
      margin-top: 20px;

      &:before {
        position: absolute;
        content: "";
        left: 50%;
        bottom: -8px;
        transform: translateX(-50%);
        width: 62px;
        height: 2px;
        background: #13AA57;
      }
    }

    .item {
      position: relative;
      margin-bottom: 20px;

      @media screen and (max-width: 767px) {
        width: 100%;
      }

      &.nimi {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/user.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }

      &.puhelinnumero {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/phone.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }

      &.sähköposti {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/email.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }

      &.postinumero {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/post.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }
    }
  }
}