header {
  padding-top: 175px;
  padding-bottom: 185px;
  background: url(../img/bg.png) no-repeat;
  background-position-x: center;
  text-align: center;

  @media screen and (max-width: 767px) {
    padding-top: 60px;
    padding-bottom: 30px;
    background-size: cover;
  }

  .logo {
    margin-bottom: 45px;

    @media screen and (max-width: 767px) {
      width: 90%;

      svg {
        width: 100%;
      }
    }
  }

  h1 {
    font-weight: bold;
    font-size: 48px;
    line-height: 62px;
    text-align: center;
    color: $secondaryText;
    margin-bottom: 30px;

    @media screen and (max-width: 767px) {
      font-size: 35px;
      line-height: 1.2;
    }
  }

  form {
    width: 850px;
    height: 396px;
    background: #FFFFFF;
    box-shadow: 0px 7px 35px rgba(90, 97, 105, 0.1), 0px 15px 22px rgba(90, 97, 105, 0.1), 0px 4px 8px rgba(90, 97, 105, 0.12), 0px 2px 3px rgba(90, 97, 105, 0.11);
    border-radius: 18px;
    padding: 30px 45px 25px;
    display: block;
    margin: 0 auto;

    @media screen and (max-width: 1023px) {
      width: 100%;
      height: auto;

      input, button {
        width: 100%;
      }
    }

    @media screen and (max-width: 767px) {
      padding: 15px;
    }

    h4 {
      font-weight: normal;
      font-size: 22px;
      line-height: 34px;
      text-align: center;
      color: #607D8B;
      margin: 0 0 25px 0;

      @media screen and (max-width: 767px) {
        font-size: 20px;
      }
    }

    h5 {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      text-transform: uppercase;
      color: #13AA57;
      position: relative;
      margin-top: 20px;

      &:before {
        position: absolute;
        content: "";
        left: 50%;
        bottom: -8px;
        transform: translateX(-50%);
        width: 62px;
        height: 2px;
        background: #13AA57;
      }
    }

    .item {
      position: relative;
      margin-bottom: 20px;

      @media screen and (max-width: 767px) {
        width: 100%;
      }

      &.nimi {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/user.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }

      &.puhelinnumero {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/phone.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }

      &.sähköposti {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/email.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }

      &.postinumero {

        &:before {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 20px;
          content: "";
          width: 20px;
          height: 20px;
          background: url(../img/post.svg) 50% 50% no-repeat;
        }

        &:after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 55px;
          content: "";
          width: 2px;
          height: 29px;
          background: #EDEDEF;
        }
      }
    }
  }
}