footer {
  padding-top: 40px;

  @media screen and (max-width: 767px) {
    padding-top: 10px;
  }

  .flex__wrap {
    padding-bottom: 30px;
    border-bottom: 1px solid $light;

    .item {

      @media screen and (max-width: 767px) {
        width: 100%;
        text-align: center;
      }

      &:nth-child(2) {
        display: flex;
        align-items: center;

        @media screen and (max-width: 767px) {
          flex-direction: column;
        }

        p {
          font-weight: 500;
          font-size: 14px;
          line-height: 16px;
          letter-spacing: 1.2px;
          text-transform: uppercase;
          color: #424753;
          margin-right: 20px;

          @media screen and (max-width: 767px) {
            margin: 20px 0;
          }
        }
      }
    }
  }

  .copyright {
    padding-top: 20px;
    text-align: center;
    padding-bottom: 40px;
    font-size: 14px;
    line-height: 17px;
    color: #607D8B;
    opacity: 0.6;
  }
}