.info {

  padding-top: 70px;
  padding-bottom: 70px;

  @media screen and (max-width: 1199px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .flex__wrap {

    @media screen and (max-width: 1199px) {
      flex-direction: column;
      align-content: center;
    }

    .item {
      width: 370px;
      height: 370px;
      padding: 40px;
      box-sizing: border-box;
      transition: all .5s;
      cursor: pointer;
      border-radius: 30px;
      background: transparent;

      @media screen and (max-width: 1199px) {
        margin-bottom: 20px;
      }

      @media screen and (max-width: 767px) {
        width: 100%;
        padding: 20px;
      }

      &:hover {
        background: url(../img/bg1.png) no-repeat;
        background-size: cover;

        .title, .description {
          color: #FFFFFF;
        }
      }

      .title {
        font-weight: 600;
        font-size: 28px;
        line-height: 33px;
        color: #424753;
        margin-top: 20px;
        margin-bottom: 20px;
        transition: all .5s;

        @media screen and (max-width: 767px) {
          font-size: 25px;
        }
      }

      .description {
        font-size: 16px;
        line-height: 32px;
        color: #424753;
        transition: all .5s;
      }
    }
  }
}