@charset "UTF-8";

/**
 * Основные стили
 **/

* {
	box-sizing: border-box;
}

html {
	font-size: 16px;
}

body {
	font-family: Raleway, sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
	box-sizing: border-box;
	min-height: 100vh;
	color: $mainText;
}

.main__wrapper {
	@include min-max(320px, 1920px, 0);
}

.wrap {
	@include min-max(320px, 1170px, 15px);
}

.main__wrapper {
	max-width: 1920px;
	margin: 0 auto;
}

.flex__wrap {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}